"use client";

import React, { useState, useRef, useEffect } from 'react';
import { Download, Sparkles, Loader2, Upload, LayoutTemplate, Save, Archive } from 'lucide-react';
import { summarizeNotes } from '../lib/geminiService';

// Import Quill styles
import 'quill/dist/quill.snow.css';

const TEMPLATES = {
  meeting: `
    <h1>📅 Meeting Minutes</h1>
    <p><strong>Date:</strong> ${new Date().toLocaleDateString()}</p>
    <p><strong>Attendees:</strong> </p>
    <h2>📝 Agenda</h2>
    <ol>
      <li>Review previous action items</li>
      <li></li>
    </ol>
    <h2>💡 Key Discussion Points</h2>
    <ul>
      <li></li>
    </ul>
    <h2>✅ Action Items</h2>
    <ul>
      <li>Task 1 (@person)</li>
    </ul>
  `,
  project: `
    <h1>🚀 Project Plan: [Project Name]</h1>
    <h2>🎯 Objectives</h2>
    <ul>
      <li>Objective 1</li>
    </ul>
    <h2>🗓️ Milestones</h2>
    <p>Phase 1 (Due TBD): 🟡</p>
    <h2>🛠️ Tech Stack</h2>
    <ul>
      <li>Frontend: </li>
      <li>Backend: </li>
    </ul>
  `,
  journal: `
    <h1>📔 Daily Journal - ${new Date().toLocaleDateString()}</h1>
    <h2>🧠 Mood & Mindset</h2>
    <p></p>
    <h2>🏆 Top 3 Wins</h2>
    <ol>
      <li></li>
      <li></li>
      <li></li>
    </ol>
    <h2>🚧 Challenges</h2>
    <p></p>
  `,
  code_docs: `
    <h1>📦 Component Documentation</h1>
    <h2>Description</h2>
    <p>Brief description of what this component does.</p>
    <h2>Usage</h2>
    <pre class="ql-syntax" spellcheck="false">
&lt;Component id="123" /&gt;
    </pre>
  `
};

const Notes = ({ searchQuery = '' }) => {
  // Initialize from localStorage or fallback
  const [content, setContent] = useState(() => {
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('nexus_notes_content_html');
      return saved || `<h1>Lecture 4: React Hooks</h1><ul><li>useState</li><li>useEffect</li></ul>`;
    }
    return `<h1>Lecture 4: React Hooks</h1><ul><li>useState</li><li>useEffect</li></ul>`;
  });
  
  const [isSummarizing, setIsSummarizing] = useState(false);
  const [showTemplates, setShowTemplates] = useState(false);
  const [lastSaved, setLastSaved] = useState(null);
  
  const fileInputRef = useRef(null);
  const contentRef = useRef(content); // Ref to hold latest content for interval/unmount
  
  // Quill Refs
  const editorContainerRef = useRef(null);
  const quillInstanceRef = useRef(null);

  // Keep ref in sync with state
  useEffect(() => {
    contentRef.current = content;
  }, [content]);

  // Initialize Quill manually to avoid findDOMNode error
  useEffect(() => {
    let quill;
    const initQuill = async () => {
      if (editorContainerRef.current && !quillInstanceRef.current) {
        // Dynamic import to avoid SSR issues
        const QuillModule = (await import('quill')).default;
        
        quill = new QuillModule(editorContainerRef.current, {
            theme: 'snow',
            placeholder: 'Initialize neural inputs...',
            modules: {
                toolbar: [
                    [{ 'header': [1, 2, 3, false] }],
                    ['bold', 'italic', 'underline', 'strike'],
                    [{ 'list': 'ordered'}, { 'list': 'bullet' }],
                    ['code-block', 'blockquote'],
                    ['clean']
                ]
            }
        });

        quillInstanceRef.current = quill;

        // Set initial content
        if (contentRef.current) {
            quill.clipboard.dangerouslyPasteHTML(contentRef.current);
        }

        // Listen for changes
        quill.on('text-change', () => {
            const html = quill.root.innerHTML;
            setContent(html);
        });
      }
    };

    initQuill();

    return () => {
        // Cleanup if needed, though Quill doesn't have a destroy method in v2 the same way
        // We just clear the ref
        // quillInstanceRef.current = null; 
    };
  }, []);

  // Auto-save effect
  useEffect(() => {
    const saveInterval = setInterval(() => {
      if (contentRef.current) {
        localStorage.setItem('nexus_notes_content_html', contentRef.current);
        setLastSaved(new Date());
      }
    }, 5000); // Auto-save every 5s

    return () => clearInterval(saveInterval);
  }, []);

  // Handle Search Highlighting (Simple implementation)
  useEffect(() => {
    if (searchQuery && quillInstanceRef.current) {
        // Clear previous formatting (this is complex in Quill, skipping for now to avoid breaking content)
        // A real implementation would use a custom blot or formatText
    }
  }, [searchQuery]);

  const handleSummarize = async () => {
    if (!quillInstanceRef.current) return;
    
    setIsSummarizing(true);
    const text = quillInstanceRef.current.getText();
    
    try {
      const summary = await summarizeNotes(text);
      
      // Append summary to the end
      const currentHtml = quillInstanceRef.current.root.innerHTML;
      const summaryHtml = `
        <br/>
        <div style="background-color: rgba(16, 185, 129, 0.1); padding: 10px; border-left: 3px solid #10b981; margin-top: 20px;">
          <h3>✨ AI Summary</h3>
          ${summary.replace(/\n/g, '<br/>')}
        </div>
        <br/>
      `;
      
      quillInstanceRef.current.clipboard.dangerouslyPasteHTML(quillInstanceRef.current.getLength(), summaryHtml);
      
    } catch (error) {
      console.error("Summarization failed", error);
    } finally {
      setIsSummarizing(false);
    }
  };

  const applyTemplate = (templateKey) => {
    if (quillInstanceRef.current && TEMPLATES[templateKey]) {
        quillInstanceRef.current.clipboard.dangerouslyPasteHTML(TEMPLATES[templateKey]);
        setShowTemplates(false);
    }
  };

  const handleExport = () => {
    const blob = new Blob([content], { type: 'text/html' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `nexus-note-${new Date().toISOString().split('T')[0]}.html`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleImport = (e) => {
    const file = e.target.files?.[0];
    if (file) {
        const reader = new FileReader();
        reader.onload = (event) => {
            const text = event.target?.result;
            if (typeof text === 'string' && quillInstanceRef.current) {
                quillInstanceRef.current.clipboard.dangerouslyPasteHTML(text);
            }
        };
        reader.readAsText(file);
    }
  };

  return (
    <div className="flex flex-col h-full relative">
      {/* Toolbar Actions */}
      <div className="flex items-center justify-between mb-2 px-1">
        <div className="flex gap-1">
            <div className="relative">
                <button 
                    onClick={() => setShowTemplates(!showTemplates)}
                    className="p-1.5 hover:bg-white/10 rounded text-muted-foreground hover:text-primary transition-colors"
                    title="Templates"
                >
                    <LayoutTemplate size={16} />
                </button>
                
                {showTemplates && (
                    <div className="absolute top-full left-0 mt-1 w-48 bg-surface border border-white/10 rounded-lg shadow-xl z-50 overflow-hidden">
                        {Object.keys(TEMPLATES).map(key => (
                            <button
                                key={key}
                                onClick={() => applyTemplate(key)}
                                className="w-full text-left px-4 py-2 text-sm hover:bg-white/5 capitalize"
                            >
                                {key.replace('_', ' ')}
                            </button>
                        ))}
                    </div>
                )}
            </div>

            <button 
                onClick={() => fileInputRef.current?.click()}
                className="p-1.5 hover:bg-white/10 rounded text-muted-foreground hover:text-primary transition-colors"
                title="Import Note"
            >
                <Upload size={16} />
            </button>
            <input 
                type="file" 
                ref={fileInputRef} 
                className="hidden" 
                accept=".html,.txt" 
                onChange={handleImport}
            />

            <button 
                onClick={handleExport}
                className="p-1.5 hover:bg-white/10 rounded text-muted-foreground hover:text-primary transition-colors"
                title="Export Note"
            >
                <Download size={16} />
            </button>
        </div>

        <div className="flex items-center gap-2">
            {lastSaved && (
                <span className="text-[10px] text-muted-foreground flex items-center gap-1">
                    <Save size={10} />
                    {lastSaved.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'})}
                </span>
            )}
            
            <button
                onClick={handleSummarize}
                disabled={isSummarizing}
                className="flex items-center gap-1 text-[10px] bg-primary/10 border border-primary/30 text-primary px-2 py-1 rounded hover:bg-primary hover:text-white transition-all disabled:opacity-50"
            >
                {isSummarizing ? <Loader2 size={12} className="animate-spin" /> : <Sparkles size={12} />}
                Summarize
            </button>
        </div>
      </div>

      {/* Editor Area */}
      <div className="flex-1 bg-black/20 rounded-xl overflow-hidden flex flex-col relative">
         <div ref={editorContainerRef} className="h-full text-foreground" />
      </div>
      
      <style jsx global>{`
        .ql-toolbar {
            border: none !important;
            border-bottom: 1px solid rgba(255,255,255,0.1) !important;
            padding: 8px !important;
        }
        .ql-container {
            border: none !important;
            font-family: inherit !important;
            font-size: 0.95rem !important;
        }
        .ql-editor {
            padding: 16px !important;
        }
        .ql-editor.ql-blank::before {
            color: rgba(255,255,255,0.3) !important;
            font-style: italic;
        }
      `}</style>
    </div>
  );
};

export default Notes;
