"use client";

import React, { useState, useRef, useEffect } from 'react';
import { Send, Bot, User, Sparkles, Terminal, Code2, Zap, Quote, Copy, Check } from 'lucide-react';
import { generateAIResponse } from '../lib/geminiService';

// --- Syntax Highlighter Component ---
const SyntaxHighlight = ({ code }) => {
  // Regex to split code into tokens: Strings, Comments, Keywords, Numbers, Classes
  const tokenRegex = /("(?:[^"\\]|\\.)*"|'(?:[^'\\]|\\.)*'|`(?:[^`\\]|\\.)*`|\/\/[^\n]*|\/\*[\s\S]*?\*\/|\b(?:const|let|var|function|return|if|else|for|while|do|switch|case|break|continue|default|try|catch|finally|throw|new|this|typeof|instanceof|void|delete|in|of|class|extends|implements|interface|type|public|private|protected|static|readonly|async|await|import|export|from|as|true|false|null|undefined)\b|\b\d+(?:\.\d+)?\b|\b[A-Z][a-zA-Z0-9_$]*\b)/g;

  const parts = code.split(tokenRegex);

  return (
    <>
      {parts.map((part, i) => {
        if (!part) return null;
        
        // String
        if (/^["'`]/.test(part)) return <span key={i} className="text-emerald-400">{part}</span>;
        
        // Comment
        if (part.startsWith('//') || part.startsWith('/*')) return <span key={i} className="text-gray-500 italic">{part}</span>;
        
        // Keyword
        if (/^(const|let|var|function|return|if|else|for|while|do|switch|case|break|continue|default|try|catch|finally|throw|new|this|typeof|instanceof|void|delete|in|of|class|extends|implements|interface|type|public|private|protected|static|readonly|async|await|import|export|from|as|true|false|null|undefined)$/.test(part)) {
           return <span key={i} className="text-pink-400 font-semibold">{part}</span>;
        }
        
        // Number
        if (/^\d+(?:\.\d+)?$/.test(part)) return <span key={i} className="text-amber-400">{part}</span>;
        
        // Class/Type (Capitalized words that aren't keywords)
        if (/^[A-Z][a-zA-Z0-9_$]*$/.test(part)) return <span key={i} className="text-yellow-200">{part}</span>;

        return <span key={i}>{part}</span>;
      })}
    </>
  );
};

// Helper to handle **bold**, *italic*, `code`, and [links](url)
const parseInline = (text) => {
  // Regex to split by markdown tokens
  const parts = text.split(/(`[^`]+`|\*\*[^*]+\*\*|_[^_]+_|\[[^\]]+\]\([^)]+\))/g);
  
  return parts.map((part, i) => {
    // Inline Code
    if (part.startsWith('`') && part.endsWith('`')) {
      return <code key={i} className="bg-white/10 text-primary px-1.5 py-0.5 rounded font-mono text-xs mx-0.5 border border-white/5">{part.slice(1, -1)}</code>;
    }
    // Bold
    if (part.startsWith('**') && part.endsWith('**')) {
      return <strong key={i} className="text-white font-semibold">{part.slice(2, -2)}</strong>;
    }
    // Italic
    if (part.startsWith('_') && part.endsWith('_')) {
       return <em key={i} className="italic text-gray-400">{part.slice(1, -1)}</em>;
    }
    // Links
    const linkMatch = part.match(/^\[([^\]]+)\]\(([^)]+)\)$/);
    if (linkMatch) {
        return (
            <a 
                key={i} 
                href={linkMatch[2]} 
                target="_blank" 
                rel="noreferrer" 
                className="text-primary underline decoration-primary/30 hover:decoration-primary transition-all hover:text-white"
            >
                {linkMatch[1]}
            </a>
        );
    }
    
    return part;
  });
};

// --- Helper: Render Regular Markdown Content (Split logic for reuse) ---
const RenderedMarkdownBlock = ({ content }) => {
    const lines = content.split('\n');
    const renderedNodes = [];

    for (let i = 0; i < lines.length; i++) {
        const line = lines[i];
        const trimmedLine = line.trim();

        if (!trimmedLine) {
            renderedNodes.push(<div key={`spacer-${i}`} className="h-2" />);
            continue;
        }

        // Horizontal Rule
        if (trimmedLine.match(/^(\*{3,}|-{3,}|_{3,})$/)) {
            renderedNodes.push(
              <hr 
                key={`hr-${i}`} 
                className="my-6 border-none h-px bg-gradient-to-r from-transparent via-white/20 to-transparent" 
              />
            );
            continue;
        }

        // Headings
        if (line.startsWith('# ')) {
            renderedNodes.push(<h1 key={`h1-${i}`} className="text-2xl font-bold text-white mt-8 mb-4 tracking-tight border-b border-white/10 pb-2">{parseInline(line.slice(2))}</h1>);
            continue;
        }
        if (line.startsWith('## ')) {
            renderedNodes.push(<h2 key={`h2-${i}`} className="text-xl font-bold text-white mt-6 mb-3 pb-1">{parseInline(line.slice(3))}</h2>);
            continue;
        }
        if (line.startsWith('### ')) {
            renderedNodes.push(<h3 key={`h3-${i}`} className="text-lg font-bold text-primary mt-4 mb-2">{parseInline(line.slice(4))}</h3>);
            continue;
        }

        // Blockquote (Grouped)
        if (line.startsWith('>')) {
            const bqLines = [];
            while (i < lines.length && lines[i].startsWith('>')) {
                bqLines.push(lines[i].replace(/^>\s?/, ''));
                i++;
            }
            i--; 

            renderedNodes.push(
                <blockquote key={`bq-${i}`} className="flex gap-3 pl-4 py-3 my-4 border-l-4 border-secondary bg-white/5 rounded-r-xl shadow-sm">
                    <Quote size={18} className="text-secondary shrink-0 mt-1 opacity-80 fill-secondary/20" />
                    <div className="text-gray-300 italic leading-7 font-medium opacity-90">
                        {bqLines.map((bqLine, idx) => (
                            <div key={idx}>{parseInline(bqLine)}</div>
                        ))}
                    </div>
                </blockquote>
            );
            continue;
        }

        // Lists (Nested)
        const listMatch = line.match(/^(\s*)([-*+]|\d+\.)\s+(.*)/);
        if (listMatch) {
            const prefix = listMatch[1];
            const spaceCount = prefix.replace(/\t/g, '    ').length;
            const indentLevel = Math.floor(spaceCount / 2);
            
            const marker = listMatch[2];
            const content = listMatch[3];
            const isOrdered = /^\d+\./.test(marker);

            const bulletStyles = ['●', '○', '■'];
            const bulletChar = bulletStyles[indentLevel % bulletStyles.length];
            
            renderedNodes.push(
               <div 
                 key={`list-${i}`} 
                 className="flex gap-2 items-start my-1"
                 style={{ marginLeft: `${indentLevel * 1.5}rem` }}
               >
                 <span className={`shrink-0 flex justify-center min-w-[1.25rem] ${isOrdered ? 'text-primary font-mono text-xs font-bold mt-1' : 'text-secondary text-[8px] mt-2'}`}>
                    {isOrdered ? marker : bulletChar}
                 </span>
                 <span className="text-gray-200 leading-relaxed">{parseInline(content)}</span>
               </div>
            );
            continue;
        }

        // Standard Paragraph
        renderedNodes.push(
            <p key={`p-${i}`} className="mb-2 leading-7">
              {parseInline(line)}
            </p>
        );
    }
    return <>{renderedNodes}</>;
};

// --- Code Block Component with Copy Functionality ---
const CodeBlock = ({ language, code }) => {
    const [copied, setCopied] = useState(false);

    const handleCopy = async () => {
        try {
            await navigator.clipboard.writeText(code);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            console.error('Failed to copy!', err);
        }
    };

    return (
        <div className="relative group my-4 rounded-xl overflow-hidden border border-white/10 bg-[#0F0F13] shadow-lg">
            <div className="flex items-center justify-between px-4 py-2 bg-white/5 border-b border-white/5">
                <div className="flex items-center gap-3">
                     <div className="flex gap-1.5">
                        <div className="w-2.5 h-2.5 rounded-full bg-red-500/20 border border-red-500/30" />
                        <div className="w-2.5 h-2.5 rounded-full bg-yellow-500/20 border border-yellow-500/30" />
                        <div className="w-2.5 h-2.5 rounded-full bg-green-500/20 border border-green-500/30" />
                     </div>
                     <span className="text-[10px] font-mono text-gray-400 lowercase">{language || 'code'}</span>
                </div>
                
                <button 
                    onClick={handleCopy}
                    className="flex items-center gap-1.5 text-gray-500 hover:text-white transition-colors p-1 rounded-md hover:bg-white/10"
                    title="Copy to Clipboard"
                >
                    {copied ? (
                        <>
                            <Check size={12} className="text-green-400" />
                            <span className="text-[10px] text-green-400 font-semibold">Copied!</span>
                        </>
                    ) : (
                        <>
                             <Copy size={12} />
                             <span className="text-[10px]">Copy</span>
                        </>
                    )}
                </button>
            </div>
            <div className="p-4 overflow-x-auto custom-scrollbar">
                <pre className="font-mono text-xs text-primary/90 leading-relaxed">
                    <SyntaxHighlight code={code} />
                </pre>
            </div>
        </div>
    );
};

// --- Markdown Renderer Component ---
const MarkdownRenderer = ({ content }) => {
  // 1. Split by Code Blocks
  const parts = content.split(/(```[\s\S]*?```)/g);

  return (
    <div className="space-y-4 text-sm leading-7 text-gray-300">
      {parts.map((part, index) => {
        // Handle Code Blocks
        if (part.startsWith('```')) {
          const match = part.match(/```(\w+)?\n([\s\S]*?)```/);
          const lang = match ? match[1]?.toLowerCase() : '';
          const code = match ? match[2] : part.replace(/```/g, '');
          
          // If language is explicitly 'text' or 'markdown', UNWRAP it (render as rich text).
          if (lang === 'text' || lang === 'markdown' || lang === 'txt') {
             return <div key={index}><RenderedMarkdownBlock content={code} /></div>;
          }

          // Otherwise, render as a Code Block with Copy Button
          return <CodeBlock key={index} language={lang} code={code} />;
        }

        // Handle Regular Text Parts
        return <div key={index}><RenderedMarkdownBlock content={part} /></div>;
      })}
    </div>
  );
};

const AIChat = ({ widgetSize }) => {
  const [input, setInput] = useState('');
  const [messages, setMessages] = useState([]); 
  const [loading, setLoading] = useState(false);
  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages, loading]);

  const handleSend = async (textOverride) => {
    const textToSend = textOverride || input;
    if (!textToSend.trim() || loading) return;

    const userMsg = {
      id: Date.now().toString(),
      role: 'user',
      text: textToSend,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMsg]);
    setInput('');
    setLoading(true);

    try {
      const history = messages.map(m => ({ role: m.role, text: m.text }));
      const responseText = await generateAIResponse(textToSend, history);
      
      const aiMsg = {
        id: (Date.now() + 1).toString(),
        role: 'model',
        text: responseText,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, aiMsg]);
    } catch (e) {
      console.error(e);
    } finally {
      setLoading(false);
    }
  };

  const suggestions = [
    { icon: <Code2 size={14} />, text: "Refactor this React component" },
    { icon: <Terminal size={14} />, text: "Explain this error message" },
    { icon: <Zap size={14} />, text: "Draft a daily schedule" },
  ];

  // Determine responsiveness
  const isCompact = widgetSize && widgetSize.rows === 1;

  return (
    <div className="flex flex-col h-full relative">
      <div className="flex-1 overflow-y-auto pr-2 custom-scrollbar">
        {messages.length === 0 ? (
           // Empty State / Suggestions
           <div className={`h-full flex flex-col items-center justify-center text-center opacity-80 ${isCompact ? 'py-1' : 'py-4'}`}>
              
              {!isCompact && (
                <div className="w-16 h-16 bg-secondary/20 rounded-full flex items-center justify-center mb-4 animate-pulse-slow">
                    <Bot size={32} className="text-secondary" />
                </div>
              )}
              
              <h3 className={`text-white font-semibold ${isCompact ? 'text-sm mb-1' : 'text-lg mb-1'}`}>Nexus Core Online</h3>
              
              {!isCompact && (
                 <p className="text-gray-500 text-xs max-w-[200px] mb-6">System ready. Awaiting neural input for processing.</p>
              )}
              
              {!isCompact && (
                  <div className="grid grid-cols-1 gap-2 w-full max-w-xs">
                     {suggestions.map((s, i) => (
                        <button 
                          key={i}
                          onClick={() => handleSend(s.text)}
                          className="flex items-center gap-3 p-3 rounded-xl bg-white/5 hover:bg-white/10 border border-white/5 hover:border-primary/30 transition-all text-sm text-gray-300 hover:text-white text-left group"
                        >
                           <span className="p-1.5 rounded-md bg-black/20 text-primary group-hover:bg-primary group-hover:text-black transition-colors">{s.icon}</span>
                           {s.text}
                        </button>
                     ))}
                  </div>
              )}
           </div>
        ) : (
            // Chat History
            <div className="space-y-6 pt-2 pb-4">
                {messages.map((msg) => (
                <div key={msg.id} className={`flex ${msg.role === 'user' ? 'justify-end' : 'justify-start'}`}>
                    <div 
                    className={`
                        max-w-[90%] md:max-w-[85%] p-4 rounded-2xl
                        ${msg.role === 'user' 
                        ? 'bg-secondary text-white rounded-br-none' 
                        : 'bg-surface/90 border border-white/10 text-gray-200 rounded-bl-none shadow-sm'}
                    `}
                    >
                        <div className="flex items-center gap-2 mb-2 opacity-60 text-[10px] uppercase tracking-wider font-bold">
                            {msg.role === 'model' ? <Bot size={12} /> : <User size={12} />}
                            <span>{msg.role === 'model' ? 'Nexus AI' : 'You'}</span>
                        </div>
                        
                        {/* Render Content using Markdown Renderer for AI, Text for User */}
                        {msg.role === 'model' ? (
                            <MarkdownRenderer content={msg.text} />
                        ) : (
                            <div className="whitespace-pre-wrap text-sm leading-relaxed font-medium">{msg.text}</div>
                        )}
                    </div>
                </div>
                ))}
                {loading && (
                <div className="flex justify-start">
                    <div className="bg-surface/50 border border-white/10 p-3 rounded-2xl rounded-bl-none text-sm text-primary flex items-center gap-2 animate-pulse">
                        <Sparkles size={14} className="animate-spin" />
                        <span className="text-xs font-mono">Processing Data...</span>
                    </div>
                </div>
                )}
                <div ref={messagesEndRef} />
            </div>
        )}
      </div>

      <div className={`mt-2 flex gap-2 pt-2 border-t border-white/5 ${isCompact ? 'mb-1' : ''}`}>
        <input 
          type="text" 
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => e.key === 'Enter' && handleSend()}
          placeholder={isCompact ? "Ask..." : "Ask Gemini..."}
          className={`flex-1 bg-black/20 border border-white/10 rounded-xl px-4 text-sm text-white focus:outline-none focus:border-primary transition-colors font-mono placeholder:text-gray-600 ${isCompact ? 'py-2' : 'py-3'}`}
        />
        <button 
          onClick={() => handleSend()}
          disabled={loading}
          className={`bg-primary hover:bg-primary/80 text-white rounded-xl transition-transform active:scale-95 disabled:opacity-50 flex items-center justify-center ${isCompact ? 'p-2' : 'p-3'}`}
        >
          <Send size={18} />
        </button>
      </div>
    </div>
  );
};

export default AIChat;
