import React, { useState, useMemo, useEffect } from "react";
import {
  CheckCircle2,
  Circle,
  Plus,
  BellRing,
  AlertCircle,
  CalendarClock,
  AlertTriangle,
  Flame,
  Star,
  ArrowDown,
  Minus,
  Filter,
  ArrowUpNarrowWide,
  ArrowDownNarrowWide,
  Clock,
  Tag as TagIcon,
  X,
  Bell,
  AlarmClock,
  Trash2,
  AlignLeft,
} from "lucide-react";

const Tasks = ({ searchQuery = "", widgetSize }) => {
  const [tasks, setTasks] = useState([
    {
      id: "1",
      title: "Complete Project Report",
      description:
        "Finalize the executive summary, check formatted tables, and export to PDF for submission.",
      deadline: new Date(Date.now() + 86400000),
      completed: false,
      priority: "high",
      tags: ["uni"],
    },
    {
      id: "2",
      title: "Review Calculus III",
      description:
        "Focus on partial derivatives and multiple integrals. Chapter 4 & 5 practice problems.",
      deadline: new Date(Date.now() - 86400000),
      completed: false,
      priority: "medium",
      tags: ["study"],
    },
    {
      id: "3",
      title: "Buy Groceries",
      description: "Milk, Eggs, Bread, Coffee beans.",
      deadline: new Date(Date.now() + 172800000),
      completed: true,
      priority: "low",
      tags: ["personal"],
    },
    {
      id: "4",
      title: "System Backup",
      description: "Run full incremental backup to NAS.",
      deadline: new Date(Date.now() + 432000000),
      completed: false,
      priority: "low",
      tags: ["tech"],
    },
  ]);

  const [newTask, setNewTask] = useState("");
  const [newDeadline, setNewDeadline] = useState("");
  const [newPriority, setNewPriority] = useState("medium");
  const [newTag, setNewTag] = useState("");

  // Reminder State
  const [newReminder, setNewReminder] = useState("");
  const [showReminderInput, setShowReminderInput] = useState(false);

  // Filter & Sort State
  const [filterStatus, setFilterStatus] = useState("active");
  const [filterTag, setFilterTag] = useState("all");
  const [sortBy, setSortBy] = useState("none");
  const [sortOrder, setSortOrder] = useState("asc");
  const [showFilters, setShowFilters] = useState(false);

  // Detail View State
  const [selectedTaskId, setSelectedTaskId] = useState(null);

  // Responsiveness checks
  const isCompactHeight = widgetSize && widgetSize.rows === 1;
  const isCompactWidth = widgetSize && widgetSize.cols === 1;

  // --- Effects ---

  // Request Notification Permission on mount
  useEffect(() => {
    if (
      typeof window !== "undefined" &&
      "Notification" in window &&
      Notification.permission === "default"
    ) {
      Notification.requestPermission();
    }
  }, []);

  // Poll for Reminders
  useEffect(() => {
    const timer = setInterval(() => {
      const now = new Date();
      setTasks((currentTasks) => {
        let updates = false;
        const nextTasks = currentTasks.map((task) => {
          // Check if reminder exists, hasn't been sent, and time has passed
          if (
            task.reminderTime &&
            !task.reminderSent &&
            new Date(task.reminderTime) <= now
          ) {
            // Trigger Notification
            if (
              typeof window !== "undefined" &&
              "Notification" in window &&
              Notification.permission === "granted"
            ) {
              new Notification(`⏰ Nexus Reminder: ${task.title}`, {
                body: `Priority: ${task.priority.toUpperCase()} | Due: ${task.deadline.toLocaleDateString()}`,
                icon: "/favicon.ico", // Ensure this exists or use a placeholder
                requireInteraction: true,
              });
            } else {
              // Fallback sound or alert could go here
              console.log("Reminder triggered:", task.title);
            }

            updates = true;
            return { ...task, reminderSent: true };
          }
          return task;
        });
        return updates ? nextTasks : currentTasks;
      });
    }, 5000); // Check every 5 seconds

    return () => clearInterval(timer);
  }, []);

  // Close detail view if selected task is deleted
  useEffect(() => {
    if (selectedTaskId && !tasks.find((t) => t.id === selectedTaskId)) {
      setSelectedTaskId(null);
    }
  }, [tasks, selectedTaskId]);

  // --- Logic ---

  const toggleTask = (id) => {
    setTasks(
      tasks.map((t) => (t.id === id ? { ...t, completed: !t.completed } : t))
    );
  };

  const cyclePriority = () => {
    const cycle = {
      low: "medium",
      medium: "high",
      high: "low",
    };
    setNewPriority(cycle[newPriority]);
  };

  const addTask = () => {
    if (!newTask.trim()) return;

    let deadlineDate;
    if (newDeadline) {
      const [y, m, d] = newDeadline.split("-").map(Number);
      deadlineDate = new Date(y, m - 1, d, 23, 59, 59);
    } else {
      deadlineDate = new Date();
      deadlineDate.setDate(deadlineDate.getDate() + 1);
      deadlineDate.setHours(23, 59, 59);
    }

    // Extract tags from text if present (e.g. "Buy milk #personal")
    let titleToSave = newTask;
    const extractedTags = [];
    const tagMatch = newTask.match(/#(\w+)/g);
    if (tagMatch) {
      tagMatch.forEach((t) => extractedTags.push(t.substring(1)));
      titleToSave = newTask.replace(/#(\w+)/g, "").trim();
    }
    if (newTag.trim()) extractedTags.push(newTag.trim());

    const task = {
      id: Date.now().toString(),
      title: titleToSave,
      description: "", // Default empty description
      deadline: deadlineDate,
      completed: false,
      priority: newPriority,
      tags: extractedTags,
      reminderTime: newReminder ? new Date(newReminder) : undefined,
      reminderSent: false,
    };

    setTasks([task, ...tasks]);

    // Reset inputs
    setNewTask("");
    setNewDeadline("");
    setNewPriority("medium");
    setNewTag("");
    setNewReminder("");
    setShowReminderInput(false);
  };

  const sendWhatsAppNotification = () => {
    alert(
      "Sending WhatsApp notification to registered number: \n\n" +
        tasks
          .filter((t) => !t.completed)
          .map((t) => `- ${t.title} [${t.priority.toUpperCase()}]`)
          .join("\n")
    );
  };

  const triggerManualReminder = (task, e) => {
    e.stopPropagation();
    alert(`Manual Reminder: "${task.title}" is currently due!`);
  };

  const isTaskOverdue = (task) => !task.completed && new Date() > task.deadline;

  // --- Derived Data ---

  const selectedTask = useMemo(
    () => tasks.find((t) => t.id === selectedTaskId),
    [tasks, selectedTaskId]
  );

  const uniqueTags = useMemo(() => {
    const tags = new Set();
    tasks.forEach((t) => t.tags.forEach((tag) => tags.add(tag)));
    return Array.from(tags);
  }, [tasks]);

  const processedTasks = useMemo(() => {
    let result = [...tasks];

    // 1. Global Search
    if (searchQuery) {
      const lowerQ = searchQuery.toLowerCase();
      result = result.filter(
        (t) =>
          t.title.toLowerCase().includes(lowerQ) ||
          (t.description && t.description.toLowerCase().includes(lowerQ)) ||
          t.tags.some((tag) => tag.toLowerCase().includes(lowerQ))
      );
    }

    // 2. Filter Status
    if (filterStatus === "active") result = result.filter((t) => !t.completed);
    else if (filterStatus === "completed")
      result = result.filter((t) => t.completed);

    // 3. Filter Tag
    if (filterTag !== "all") {
      result = result.filter((t) => t.tags.includes(filterTag));
    }

    // 4. Sort
    if (sortBy !== "none") {
      result.sort((a, b) => {
        let comparison = 0;
        if (sortBy === "deadline") {
          comparison = a.deadline.getTime() - b.deadline.getTime();
        } else if (sortBy === "priority") {
          const pMap = { high: 3, medium: 2, low: 1 };
          comparison = pMap[a.priority] - pMap[b.priority];
        }
        return sortOrder === "asc" ? comparison : -comparison;
      });
    }

    return result;
  }, [tasks, searchQuery, filterStatus, filterTag, sortBy, sortOrder]);

  const handleSortToggle = (type) => {
    if (sortBy === type) {
      if (sortOrder === "asc") setSortOrder("desc");
      else setSortBy("none");
    } else {
      setSortBy(type);
      setSortOrder("asc");
    }
  };

  // --- Helper Renderers ---

  const getPriorityIcon = (priority) => {
    switch (priority) {
      case "high":
        return <Flame size={10} />;
      case "medium":
        return <Minus size={10} />;
      case "low":
        return <ArrowDown size={10} />;
      default:
        return <Minus size={10} />;
    }
  };

  const getPriorityColorClass = (priority) => {
    switch (priority) {
      case "high":
        return "text-amber-400 bg-amber-400/10 border-amber-400/20";
      case "medium":
        return "text-nexus-teal bg-nexus-teal/10 border-nexus-teal/20";
      case "low":
        return "text-green-400 bg-green-400/10 border-green-400/20";
      default:
        return "text-gray-400";
    }
  };

  const renderTaskItem = (task) => {
    const overdue = isTaskOverdue(task);
    const isImportant = task.priority === "high";
    const hasReminder = !!task.reminderTime;

    let containerClass =
      "hover:bg-white/5 border-l-2 border-transparent hover:border-nexus-purple/50 transition-all duration-300 ease-out";
    let titleClass = "text-white transition-all duration-300";
    let icon = (
      <Circle
        size={18}
        className="text-gray-500 group-hover:text-nexus-purple transition-colors duration-300"
      />
    );
    let iconWrapperClass = "scale-100 transition-transform duration-300";

    if (task.completed) {
      titleClass =
        "text-green-400 decoration-slate-500 line-through opacity-60";
      icon = <CheckCircle2 size={18} className="text-nexus-teal" />;
      containerClass =
        "bg-green-500/5 border-l-2 border-green-500/30 opacity-70 hover:opacity-100 transition-all duration-300";
      iconWrapperClass = "scale-110";
    } else if (overdue) {
      containerClass =
        "bg-gradient-to-r from-red-900/20 to-transparent border-l-2 border-red-500 transition-all duration-300";
      titleClass = "text-red-200";
      icon = <AlertTriangle size={18} className="text-red-500 animate-pulse" />;
    } else if (isImportant) {
      containerClass =
        "bg-gradient-to-r from-amber-500/10 to-transparent border-l-2 border-amber-500 transition-all duration-300";
      titleClass = "text-amber-100";
      icon = <Star size={18} className="text-amber-400 fill-amber-400/20" />;
    }

    return (
      <div
        key={task.id}
        className={`group flex items-center gap-3 rounded-r-lg cursor-pointer ${containerClass} ${
          isCompactHeight ? "p-1.5" : "p-3"
        }`}
        onClick={() => setSelectedTaskId(task.id)}
      >
        <div
          className={`${iconWrapperClass} hover:opacity-80`}
          onClick={(e) => {
            e.stopPropagation();
            toggleTask(task.id);
          }}
        >
          {icon}
        </div>

        <div className="flex-1 min-w-0">
          <div className="flex justify-between items-center gap-2">
            <p className={`text-sm font-medium truncate ${titleClass}`}>
              {task.title}
            </p>
            <div className="flex gap-1 shrink-0 items-center">
              {!isCompactWidth &&
                task.tags.map((tag) => (
                  <span
                    key={tag}
                    className="hidden sm:inline-block px-1 py-0.5 rounded bg-white/5 text-[8px] text-gray-400 border border-white/5"
                  >
                    #{tag}
                  </span>
                ))}
              {overdue && (
                <>
                  <button
                    onClick={(e) => triggerManualReminder(task, e)}
                    className="p-1 hover:bg-red-500/20 text-red-400 rounded-full transition-colors hidden group-hover:block"
                    title="Trigger Notification"
                  >
                    <BellRing size={12} className="animate-pulse" />
                  </button>
                  <span className="flex items-center gap-1 text-[9px] text-red-200 bg-red-900/60 px-2 py-0.5 rounded-full border border-red-500/30 uppercase tracking-wider font-bold shadow-[0_0_10px_rgba(220,38,38,0.2)]">
                    <AlertCircle size={10} /> Late
                  </span>
                </>
              )}
              {hasReminder && !task.completed && (
                <span
                  className={`flex items-center gap-1 text-[9px] px-1.5 py-0.5 rounded border border-nexus-teal/30 uppercase tracking-wider font-bold ${
                    task.reminderSent
                      ? "text-gray-500 bg-gray-800/50"
                      : "text-nexus-teal bg-nexus-teal/10"
                  }`}
                  title={`Reminder set for: ${task.reminderTime?.toLocaleString()}`}
                >
                  <AlarmClock size={10} />
                  {!isCompactWidth && (task.reminderSent ? "Sent" : "Set")}
                </span>
              )}
            </div>
          </div>
          <p className="text-[10px] text-gray-500 font-mono flex items-center gap-2 mt-0.5">
            <span
              className={`flex items-center gap-1 ${
                overdue ? "text-red-300" : ""
              }`}
            >
              <CalendarClock size={10} />
              {task.deadline.toLocaleDateString()}
            </span>
            <span className="text-gray-600">•</span>
            <span
              className={`flex items-center gap-1 px-1.5 py-0.5 rounded border text-[9px] font-bold uppercase tracking-wider ${getPriorityColorClass(
                task.priority
              )}`}
            >
              {getPriorityIcon(task.priority)} {task.priority}
            </span>
          </p>
        </div>
      </div>
    );
  };

  return (
    <div className="flex flex-col h-full relative">
      {/* Detail View Overlay */}
      {selectedTask && (
        <div className="absolute inset-0 bg-[#0C0C11]/95 backdrop-blur-xl z-20 flex flex-col p-4 animate-fade-in rounded-3xl border border-white/10 shadow-2xl">
          <div className="flex justify-between items-start mb-4">
            <h3 className="text-lg font-bold text-white leading-tight pr-4">
              {selectedTask.title}
            </h3>
            <button
              onClick={() => setSelectedTaskId(null)}
              className="p-1 hover:bg-white/10 rounded-full text-gray-400 hover:text-white transition-colors"
            >
              <X size={20} />
            </button>
          </div>

          <div className="space-y-4 flex-1 overflow-y-auto pr-1 custom-scrollbar">
            {/* Priority & Status */}
            <div className="flex items-center gap-3">
              <span
                className={`flex items-center gap-1 px-2 py-1 rounded border text-xs font-bold uppercase tracking-wider ${getPriorityColorClass(
                  selectedTask.priority
                )}`}
              >
                {getPriorityIcon(selectedTask.priority)} {selectedTask.priority}{" "}
                Priority
              </span>
              {selectedTask.completed ? (
                <span className="flex items-center gap-1 px-2 py-1 rounded border text-xs font-bold uppercase tracking-wider text-green-400 bg-green-400/10 border-green-400/20">
                  <CheckCircle2 size={12} /> Completed
                </span>
              ) : (
                <span className="flex items-center gap-1 px-2 py-1 rounded border text-xs font-bold uppercase tracking-wider text-gray-400 bg-gray-400/10 border-gray-400/20">
                  <Circle size={12} /> Pending
                </span>
              )}
            </div>

            {/* Description */}
            <div className="bg-white/5 rounded-xl p-3 border border-white/5">
              <h4 className="text-[10px] uppercase text-gray-500 font-bold mb-2 flex items-center gap-2">
                <AlignLeft size={12} /> Description
              </h4>
              <p className="text-sm text-gray-300 leading-relaxed whitespace-pre-wrap">
                {selectedTask.description ||
                  "No additional details provided for this mission."}
              </p>
            </div>

            {/* Metadata Grid */}
            <div className="grid grid-cols-2 gap-3">
              <div className="bg-white/5 rounded-xl p-3 border border-white/5">
                <h4 className="text-[10px] uppercase text-gray-500 font-bold mb-1 flex items-center gap-2">
                  <CalendarClock size={12} /> Deadline
                </h4>
                <p className="text-sm text-white font-mono">
                  {selectedTask.deadline.toLocaleString()}
                </p>
              </div>

              <div className="bg-white/5 rounded-xl p-3 border border-white/5">
                <h4 className="text-[10px] uppercase text-gray-500 font-bold mb-1 flex items-center gap-2">
                  <TagIcon size={12} /> Tags
                </h4>
                <div className="flex flex-wrap gap-1">
                  {selectedTask.tags.length > 0 ? (
                    selectedTask.tags.map((t) => (
                      <span
                        key={t}
                        className="text-xs bg-nexus-purple/20 text-nexus-purple px-1.5 py-0.5 rounded border border-nexus-purple/30"
                      >
                        #{t}
                      </span>
                    ))
                  ) : (
                    <span className="text-xs text-gray-500 italic">None</span>
                  )}
                </div>
              </div>
            </div>

            {/* Reminder Info */}
            {selectedTask.reminderTime && (
              <div className="bg-white/5 rounded-xl p-3 border border-white/5 flex items-center gap-3">
                <BellRing
                  size={16}
                  className={
                    selectedTask.reminderSent
                      ? "text-gray-500"
                      : "text-nexus-teal"
                  }
                />
                <div>
                  <h4 className="text-[10px] uppercase text-gray-500 font-bold">
                    Reminder
                  </h4>
                  <p className="text-xs text-white font-mono">
                    {selectedTask.reminderTime.toLocaleString()}
                    <span className="ml-2 opacity-50">
                      ({selectedTask.reminderSent ? "Sent" : "Pending"})
                    </span>
                  </p>
                </div>
              </div>
            )}
          </div>

          {/* Actions Footer */}
          <div className="pt-4 mt-2 border-t border-white/5 flex gap-2">
            <button
              onClick={() => toggleTask(selectedTask.id)}
              className={`flex-1 py-2 rounded-xl text-xs font-bold flex items-center justify-center gap-2 transition-all ${
                selectedTask.completed
                  ? "bg-white/5 text-gray-400 hover:bg-white/10"
                  : "bg-green-500/20 text-green-400 hover:bg-green-500/30"
              }`}
            >
              {selectedTask.completed ? (
                <X size={14} />
              ) : (
                <CheckCircle2 size={14} />
              )}
              {selectedTask.completed ? "Mark Incomplete" : "Complete Mission"}
            </button>

            <button
              onClick={() => {
                setTasks((prev) =>
                  prev.filter((t) => t.id !== selectedTask.id)
                );
                setSelectedTaskId(null);
              }}
              className="p-2 bg-red-500/10 text-red-400 rounded-xl hover:bg-red-500/20 transition-colors"
              title="Delete Task"
            >
              <Trash2 size={16} />
            </button>
          </div>
        </div>
      )}

      {/* Input Area */}
      <div
        className={`flex flex-col gap-2 mb-2 bg-black/20 rounded-xl border border-white/5 ${
          isCompactHeight ? "p-2" : "p-3"
        }`}
      >
        <div className="flex gap-2">
          <input
            type="text"
            value={newTask}
            onChange={(e) => setNewTask(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && addTask()}
            placeholder={
              isCompactHeight ? "Add Task..." : "New Mission... (use #tag)"
            }
            className="flex-1 bg-transparent text-sm text-white focus:outline-none font-mono placeholder:text-gray-600"
          />

          {/* Simple Add button for compact mode instead of priority cycle */}
          {isCompactHeight ? (
            <button
              onClick={addTask}
              className="p-1 rounded-lg text-nexus-teal hover:bg-nexus-teal hover:text-black transition-colors"
            >
              <Plus size={16} />
            </button>
          ) : (
            <button
              onClick={cyclePriority}
              className={`
                        px-2 py-1 rounded-lg text-[10px] font-bold uppercase tracking-wider border transition-all flex items-center gap-1
                        ${getPriorityColorClass(newPriority)}
                    `}
              title="Click to cycle priority"
            >
              {getPriorityIcon(newPriority)}
              {newPriority}
            </button>
          )}
        </div>

        {/* Extended Input Controls (Hidden in Compact Height Mode) */}
        {!isCompactHeight && (
          <div className="flex items-center justify-between border-t border-white/5 pt-2 mt-1">
            <div className="flex items-center gap-2">
              <input
                type="date"
                value={newDeadline}
                onChange={(e) => setNewDeadline(e.target.value)}
                className="bg-transparent text-xs text-gray-400 focus:outline-none focus:text-nexus-teal transition-colors font-mono max-w-[100px]"
                style={{ colorScheme: "dark" }}
                title="Deadline"
              />

              {/* Reminder Toggle */}
              <button
                onClick={() => setShowReminderInput(!showReminderInput)}
                className={`p-1.5 rounded-md transition-all ${
                  showReminderInput || newReminder
                    ? "bg-nexus-purple text-white shadow-sm"
                    : "text-gray-500 hover:text-nexus-purple hover:bg-white/5"
                }`}
                title="Set Reminder Notification"
              >
                {newReminder ? <BellRing size={14} /> : <Bell size={14} />}
              </button>

              {/* Conditional Reminder Input */}
              {showReminderInput && (
                <input
                  type="datetime-local"
                  value={newReminder}
                  onChange={(e) => setNewReminder(e.target.value)}
                  className="bg-white/5 border border-white/10 rounded px-2 py-0.5 text-[10px] text-nexus-teal focus:outline-none focus:border-nexus-teal font-mono animate-fade-in"
                  style={{ colorScheme: "dark" }}
                />
              )}
            </div>

            <div className="flex items-center gap-2">
              <button
                onClick={() => setShowFilters(!showFilters)}
                className={`p-1.5 rounded-md transition-colors ${
                  showFilters || sortBy !== "none" || filterStatus !== "all"
                    ? "text-nexus-teal bg-nexus-teal/10"
                    : "text-gray-500 hover:text-white"
                }`}
                title="Filters & Sort"
              >
                <Filter size={14} />
              </button>
              <button
                onClick={addTask}
                className="flex items-center gap-1 text-[10px] font-bold bg-nexus-teal text-nexus-deep px-3 py-1 rounded-md hover:bg-white transition-colors"
              >
                <Plus size={14} /> ADD
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Filters Toolbar */}
      {showFilters && !isCompactHeight && (
        <div className="mb-4 animate-fade-in space-y-2 bg-white/5 p-2 rounded-lg border border-white/5">
          <div className="flex items-center justify-between">
            {/* Status Filter */}
            <div className="flex bg-black/30 rounded-lg p-0.5 border border-white/5">
              {["all", "active", "completed"].map((s) => (
                <button
                  key={s}
                  onClick={() => setFilterStatus(s)}
                  className={`px-3 py-1 rounded-md text-[10px] uppercase font-bold tracking-wider transition-all ${
                    filterStatus === s
                      ? "bg-nexus-purple text-white shadow-sm"
                      : "text-gray-500 hover:text-gray-300"
                  }`}
                >
                  {s}
                </button>
              ))}
            </div>

            {/* Sort Controls */}
            <div className="flex gap-1">
              <button
                onClick={() => handleSortToggle("deadline")}
                className={`flex items-center gap-1 px-2 py-1 rounded border text-[10px] uppercase font-bold tracking-wider transition-all ${
                  sortBy === "deadline"
                    ? "bg-nexus-teal text-nexus-deep border-nexus-teal"
                    : "bg-transparent text-gray-400 border-white/10 hover:border-white/30"
                }`}
              >
                <Clock size={12} /> Date
                {sortBy === "deadline" &&
                  (sortOrder === "asc" ? (
                    <ArrowUpNarrowWide size={12} />
                  ) : (
                    <ArrowDownNarrowWide size={12} />
                  ))}
              </button>
              <button
                onClick={() => handleSortToggle("priority")}
                className={`flex items-center gap-1 px-2 py-1 rounded border text-[10px] uppercase font-bold tracking-wider transition-all ${
                  sortBy === "priority"
                    ? "bg-nexus-teal text-nexus-deep border-nexus-teal"
                    : "bg-transparent text-gray-400 border-white/10 hover:border-white/30"
                }`}
              >
                <Flame size={12} /> Prio
                {sortBy === "priority" &&
                  (sortOrder === "asc" ? (
                    <ArrowUpNarrowWide size={12} />
                  ) : (
                    <ArrowDownNarrowWide size={12} />
                  ))}
              </button>
            </div>
          </div>

          {/* Tags Filter */}
          <div className="flex gap-1 overflow-x-auto pb-1 custom-scrollbar">
            <button
              onClick={() => setFilterTag("all")}
              className={`flex items-center gap-1 px-2 py-1 rounded-full text-[10px] whitespace-nowrap border transition-all ${
                filterTag === "all"
                  ? "bg-white/20 text-white border-white/30"
                  : "bg-transparent text-gray-500 border-dashed border-white/10"
              }`}
            >
              <TagIcon size={10} /> All
            </button>
            {uniqueTags.map((tag) => (
              <button
                key={tag}
                onClick={() => setFilterTag(tag === filterTag ? "all" : tag)}
                className={`px-2 py-1 rounded-full text-[10px] whitespace-nowrap border transition-all ${
                  filterTag === tag
                    ? "bg-nexus-purple/20 text-nexus-purple border-nexus-purple"
                    : "bg-transparent text-gray-500 border-white/10 hover:border-nexus-purple/30"
                }`}
              >
                #{tag}
              </button>
            ))}
          </div>
        </div>
      )}

      {/* Task List */}
      <div className="flex-1 overflow-y-auto pr-1">
        {/* Render Logic: Default Split View vs Sorted List */}
        {sortBy === "none" ? (
          <>
            {/* Important Section */}
            {processedTasks.some(
              (t) => !t.completed && t.priority === "high"
            ) && (
              <div className="mb-6">
                <h4 className="text-[10px] font-bold text-amber-500 uppercase tracking-widest mb-2 flex items-center gap-2 pl-1 opacity-80">
                  <Flame size={12} className="fill-current" /> Critical Focus
                </h4>
                <div className="space-y-2">
                  {processedTasks
                    .filter((t) => !t.completed && t.priority === "high")
                    .map(renderTaskItem)}
                </div>
                <div className="h-px w-full bg-white/5 mt-4" />
              </div>
            )}

            {/* Regular Section */}
            <div className="space-y-2">
              {processedTasks.some(
                (t) => !t.completed && t.priority === "high"
              ) && (
                <h4 className="text-[10px] font-bold text-gray-500 uppercase tracking-widest mb-2 pl-1">
                  Backlog
                </h4>
              )}
              {processedTasks
                .filter((t) => t.completed || t.priority !== "high")
                .map(renderTaskItem)}

              {processedTasks.length === 0 && (
                <div className="text-center py-8 text-gray-600 text-xs italic">
                  {searchQuery || filterStatus !== "all"
                    ? "No missions match criteria."
                    : "No active missions."}
                </div>
              )}
            </div>
          </>
        ) : (
          /* Sorted / Filtered Flat View */
          <div className="space-y-2">
            {processedTasks.map(renderTaskItem)}
            {processedTasks.length === 0 && (
              <div className="text-center py-8 text-gray-600 text-xs italic">
                No missions match your filters.
              </div>
            )}
          </div>
        )}
      </div>

      {!isCompactHeight && (
        <button
          onClick={sendWhatsAppNotification}
          className="mt-4 flex items-center justify-center gap-2 w-full py-2 bg-green-600/20 hover:bg-green-600/40 text-green-400 text-xs rounded-lg border border-green-500/30 transition-all"
        >
          <BellRing size={14} />
          Sync to WhatsApp
        </button>
      )}
    </div>
  );
};

export default Tasks;
